@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@font-face{font-family:"alien";src:url(/Minesweeper2/static/media/alien.99e2ab9c.ttf)}#flagcounter-container{height:100%;width:90px;display:flex;justify-content:flex-start;align-items:center;font-size:2.5rem;font-family:"Lato",sans-serif;font-weight:700;color:#f0f;background-color:#000;text-shadow:0 0 10px #f0f}.icon-medium{font-size:2.5rem;color:#0008f9;text-shadow:0 0 10px #0008f9}
@font-face{font-family:"alien";src:url(/Minesweeper2/static/media/alien.99e2ab9c.ttf)}#gameselect-container{width:110px;height:100%;display:flex;justify-content:center;align-items:center;color:#f0f;text-shadow:0 0 10px #f0f;background-color:#000}.icon-medium{font-size:2.5rem}
#newgamebutton-container{width:50px;height:50px;display:flex;justify-content:center;align-items:center;border:3px solid #f0f;border-radius:5px;cursor:pointer;color:#f0f;text-shadow:0 0 5px #f0f;background-color:#000;box-shadow:0 0 10px #f0f}#newgamebutton-container:hover{box-shadow:0 0 20px #f0f}
@font-face{font-family:"alien";src:url(/Minesweeper2/static/media/alien.99e2ab9c.ttf)}#timer-container{width:110px;height:50px;border:3px solid #f0f;border-radius:4px;text-align:center;font-family:"Lato",sans-serif;font-size:2.5rem;font-weight:700;color:#0008f9;background-color:#000;text-shadow:0 0 10px #0008f9;box-shadow:0 0 10px #f0f}
#controls-container{width:100%;height:80px;border-bottom:3px solid #f0f;position:absolute;display:flex;justify-content:center;background-color:#000;z-index:2;box-shadow:0 0 15px #f0f}#controls-container #controls-cluster{width:100%;height:100%;padding:0 10px;display:flex;justify-content:space-between;align-items:center}@media(min-width: 1000px){#controls-container #controls-cluster{width:1000px}}
@font-face{font-family:"alien";src:url(/Minesweeper2/static/media/alien.99e2ab9c.ttf)}.cell-container{display:flex;justify-content:center;align-items:center;border:1px solid #0008f9;font-family:"Lato",sans-serif;font-size:1.5rem;font-weight:700;background-color:#fff}.cell-container .unexposed-cell{width:100%;height:100%;cursor:pointer;display:flex;justify-content:center;align-items:center;background-color:#f0f;transition:background-color 1s ease-out}.cell-container .unexposed-cell:hover{background-color:#0008f9}.exploded{color:#9d0000;text-shadow:0 0 20px red}.icon-tiny{font-size:1.5rem}.cell1{color:blue}.cell2{color:green}.cell3{color:red}.cell4{color:purple}.cell5{color:#000}.cell6{color:gray}.cell7{color:maroon}.cell8{color:#40e0d0}
@font-face{font-family:"alien";src:url(/Minesweeper2/static/media/alien.99e2ab9c.ttf)}#gameboard-container{width:100%;height:100%;min-height:100vh;position:absolute;top:0;z-index:0;display:flex;justify-content:center;align-items:center;flex-wrap:wrap;overflow:hidden;background-color:#000}#gameboard-area{display:flex;flex-wrap:wrap;box-shadow:0 0 30px #0008f9}.cell-row{display:flex;flex-direction:row}.result-text{width:100%;text-align:center;font-size:2.5rem;font-family:"alien";color:#f0f;text-shadow:0 0 10px #f0f}
.App{width:100vw;height:100vh;position:relative}
